.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

svg {
  display: inline-block;
  margin-left: 50px;
  margin-right: auto;
  width: 550px;
}

.yAxis {
  -ms-user-select: none;
  user-select: none;
}

.mouseListener {
  opacity: 0;
}

.selectionBox {
  opacity: 0.15;
}

.selectedGenesBox {
  display: inline-block;
  height: 400px;
  width: 150px;
  border: 1px solid #ccc;
  font: 16px/26px Georgia, Garamond, Serif;
  overflow: auto;
  white-space: pre;
}

.geneSelector {
  display: block;
  margin-left: 50px;
  margin-right: auto;
}
